	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 11 - The Drumel genes","texte":{"childMarkdownRemark":{"html":"<p>Darakei knew that Wamien would arrive with those he had asked to see to take a blood sample and would finish preparing the room where the samples would be taken.</p>\n<p>Julius : Mr Drumel, Wamien and his friends are coming, they are waiting for you at the reception.<br>\nDarakei : Thank you, Julius, can you get them and bring them here please?<br>\nJulius: Right away, sir.</p>\n<p>Julius went to meet Wamien and the others. Darakei was trying to stay professional despite her son's absence. Éléa was doing everything she could to find their child.</p>\n<p>Wamien arrived with Sam, Gabriel, Hiro, Sarah and Sandrine. Darakei smiles at them all.</p>\n<p>Darakei : Hello everyone, sorry to have brought you all this way.<br>\nWamien : Don't worry Darakei, we're here to help you in your research.<br>\nDarakei : Thank you, I invite you to take turns in the chair. Who wants to go first?<br>\nSam: I'm going to go, I hate having a blood test so I might as well get rid of it as soon as possible.</p>\n<p>Darakei sets Sam up to draw his blood and Sam takes a deep breath before closing his eyes for Darakei to take the blood sample. Wamien looks at the others.</p>\n<p>Wamien : Who goes after?<br>\nHiro: I'm going to go.<br>\nSarah: Me after and Sandrine following me.<br>\nSandrine: Don't decide for me, sister.<br>\nGabriel: I go last.</p>\n<p>Darakei finishes with Sam and places the bandage on him.</p>\n<p>Darakei: Next.</p>\n<p>Hiro will stand up and give Sam a pat on the back.</p>\n<p>Hiro: You're brave, Sam.<br>\nSam : Thank you Hiro.</p>\n<p>Hiro will settle in and Darakei will take the blood sample.</p>\n<p>Gabriel : If not Mr Drumel, what is the reason for the blood tests?<br>\nDarakei: I have a new theory about how to identify people who may have New Human genes.<br>\nGabriel: Oh! Cool!<br>\nDarakei : Of course, I can't explain how I study all this, but I can tell you that you will all help me by giving a little blood.<br>\nWamien: Sarah, it's your turn.</p>\n<p>Sarah comes to stand next to Hiro. Hiro's going pale.</p>\n<p>Sarah: Oh? You okay, Hiro?<br>\nDarakei: I finish the blood test and then put your head between your legs.<br>\nHiro: Ho-how is that?</p>\n<p>Darakei finished Hiro's swab and took his head to put between his legs.</p>\n<p>Darakei: If I let you sit like that, you'd faint. And I don't think that's what we want.</p>\n<p>Hiro was a little uncomfortable finding himself in such a position. Sarah caressed his back and Darakei straightened Hiro.</p>\n<p>Hiro: I think I'm feeling better.<br>\nDarakei: Well, let Sarah then.</p>\n<p>Hiro gets up and goes to join the others. Sarah takes her place and the other blood tests run smoothly.</p>\n<p>Darakei: Wamien, you want to come with me, I have to talk to you in private.<br>\nWamien: No problem Darakei, I'm with you. Guys, go wait for me at the reception.</p>\n<p>Darakei goes to his office and I follow him. We walk into his office and close the door after I walk in.</p>\n<p>Darakei: Sit down, it might confuse you a little what I have to say.</p>\n<p>I wonder what he has to say to me that is so important, but I'm going to sit in one of his desk chairs.</p>\n<p>Darakei: So where do we start? Do you know what GenCorp is all about?<br>\nWamien : Identify New Humans or possible New Humans and allow them to unlock their powers?<br>\nDarakei: Pretty much, but there's more to it than that. When the children are four years old, we can take a blood sample if we have the consent of the parents or guardians. So when you were four, we got a sample of your blood and we identified the potential of new human genes. So we have been watching you since the moment, without intervening directly or indirectly.<br>\nWamien : Okay, it's a little special, but I understand the idea. Of course, locking up those who have their powers activated is not very diplomatic.<br>\nDarakei : I know, my superiors aren't too keen on letting freshly activated people walk around in the open air.<br>\nWamien : Wait, wait, you have superiors?<br>\nDarakei: Yes, about ten people higher than me, but in Damonia's laboratory, I am the highest ranking person on the continent.<br>\nWamien : All this time, I thought you were the head of GenCorp and I put most of your stock on your back. And I apologize for that.<br>\nDarakei: It's okay, don't worry. I know what you mean. You wanted to protect others, that's a great thing, you know?<br>\nWamien: Of course Darakei, of course I wanted to protect my friends.<br>\nDarakei : I'm proud of what you're doing with The Guild and don't worry, we'll find a way to put the Black Eagles back behind bars.<br>\nWamien: And The Legion which is still at large too.<br>\nDarakei: Also. Come on, I'll let you go back with your friends, I have their samples to study.<br>\nWamien: Okay Darakei, have a good day.<br>\nDarakei : Thank you and enjoy your journey with La Guilde.</p>\n<p>I get up and leave Darakei's office to join the others at the reception.</p>\n<p>Hiro: You okay? You seem a little confused.<br>\nWamien : Yes yes, I'm fine. Come on, we have a long drive home.</p>\n<p>Julius is watching us leave. I don't like his little assistant face. Maybe it's just a feeling.</p>"}},"chapitreAvant":"chapter-10-william","chapitreApres":"chapter-12-theft","nomRoman":"the-first-cyborg-volume-2","slug":"chapter-11-the-drumel-genes","codeChapitre":"H0002C011","equivalentUrl":"/histoires/chapitre/chapitre-11-les-genes-drumel"}},"pathContext":{"slug":"chapter-11-the-drumel-genes"}}

